/**
 * This part of the stylesheet imports Google's Exo 2 fonts,
 * which are used as main fonts of Predictive Works website.
 */
@font-face {
  font-family: 'Exo 2';
  font-style: normal;
  font-weight: 400;
  src: url('../google-fonts/exo-2-v6-latin-regular.eot');
  src: local('Exo 2'), local('Exo2-Regular'),
       url('../google-fonts/exo-2-v6-latin-regular.eot?#iefix') format('embedded-opentype'),
       url('../google-fonts/exo-2-v6-latin-regular.woff2') format('woff2'),
       url('../google-fonts/exo-2-v6-latin-regular.woff') format('woff'),
       url('../google-fonts/exo-2-v6-latin-regular.ttf') format('truetype'),
       url('../google-fonts/exo-2-v6-latin-regular.svg#Exo2') format('svg');
}

/********************
 *
 * COMMON STYLING
 *
 *******************/

:root {

  --brand-black: #222d32;
  --brand-color: #1bacab;
  --brand-grey: #6c757d;
  --brand-white: #f1f5f8;
  --link-color: #0366d6;

  --brand-border: 5px solid var(--brand-color);

  --fa-icon-margin: 0rem 0.5rem 0rem 0.75rem;

  --header-title-font-size: 2.5rem;
  --header-title-margin: 0rem 0rem 0rem 2.5rem;

  --logo-size: 64px;
  --logo-margin: 12px 0px 0px 0px;

  --main-content-padding: 0.75rem;
  --main-nav-border: 1px solid var(--brand-white);

  --nav-container-margin: 1.0rem 0rem 1.0rem 0rem;
  --nav-container-padding: 1.25rem 0rem 1.25rem 0rem;

  --nav-article-margin: 0.5rem;
  --nav-article-padding: 0.5rem;

  --nav-title-padding: 0.75rem 0rem 0.75rem 0rem;

  --li-line-height: 1.75rem;
  --p-size: 1.15rem;
  --h3-size: 1.25rem;

  --params-size: 0.95rem;
  --params-td-padding: 0.75rem 0.5rem 0.75rem 0.5rem;

  --site-footer-height: 30px;
  --site-footer-padding: 1.5rem;

  --site-header-height: 130px;
  --site-header-title-height: 100px;

  --site-header-breadcrumbs-padding: 0.5rem 0rem 0.5rem 0rem;
  --site-header-breadcrumbs-height: 30px;

  --td-padding: 0.5rem;
  --th-padding: 0.5rem;

}

body {
  font-family: 'Exo 2';
  height: 100%;
  margin:0;
  padding:0;
}

h1, h2, h3 {
  color: var(--brand-grey);
}

h3 {
  font-size: var(--h3-size);
}

li a, li a:visited {
  color: var(--brand-grey);
  text-decoration: none;
}

ul > li {
  color: var(--brand-grey);
  font-size: var(--p-size);
  line-height: var(--li-line-height);
}

blockquote {
  margin-top: 0;
  margin-left: 0;
  border-left: var(--brand-border);
  padding-left: 1.0rem;
}

blockquote p {
  line-height: 1.5rem;
}

a , a:visited {
  color: var(--link-color);
  font-weight: 600;
  text-decoration: none;
}

p {
  color: var(--brand-grey);
  font-size: var(--p-size);
}

td {
  padding: var(--td-padding);
}

th {
  padding: var(--th-padding);
  text-align: left;
}

tr {
  font-size: var(--p-size);
}

tr:nth-child(odd) {
  background: #ffffff;
}

tr:nth-child(even) {
  background: var(--brand-white);
}

#content-toc {
  background: rgba(27, 172, 171, 0.10);
  position: sticky;
  top: var(--site-header-height);
  width: 100%;
  z-index: 10;
}

.brand-grey {
  color: var(--brand-grey);
}

.brand-teal {
  color: var(--brand-color);
}

.breadcrumb {
  display: flex;
  list-style-type: none;
}

.breadcrumb li {
  color: var(--brand-grey);
  text-decoration: none;
  text-transform: capitalize;
}

.column-content {
  width: 60%;
}

.column-left {
  width: 20%;
}

.column-right {
  width: 30%;
}

.fa-icon {
  margin: var(--fa-icon-margin);
}

.logo {
  background-image: url('../images/logo.svg');
  background-size:contain;
  background-repeat:no-repeat;
  margin: var(--logo-margin);
  height: var(--logo-size);
  width: var(--logo-size);
}

.header-content, .main-content, .site-content {
  display: flex;
}

.header-content {
  align-items: center;
  justify-content: center;
}

.header-title {
  font-size: var(--header-title-font-size);
  margin: var(--header-title-margin);
}

.main-nav {
  border-right: var(--main-nav-border);
  height: 100%;
}

.main-content {
  flex-direction: column;
  padding: var(--main-content-padding);
  height: 100%;
}

.nav-container {
  padding: var(--main-content-padding);
}

.nav-main {
  height: 100%;
}

.nav-container > a {
  text-decoration: none;
}

a .nav-article {
  color: var(--brand-grey);
  padding: var(--nav-article-padding);
}

a:hover .nav-article {
  background: var(--brand-color);
  border-radius: 0.17rem;
  color: #ffffff !important;
}

a:visited .nav-article {
  color: var(--brand-grey);
  text-decoration: none;
}

.nav-title {
  font-weight: 600;
  padding: var(--nav-title-padding);
  text-transform: uppercase;
}

.page-title {
  color: var(--brand-grey);
  font-size: 2em;
  font-weight: bold;
  margin: 0.67rem 0rem 0.67rem 0rem;
}

.params tr {
  font-size: var(--params-size);
}

.params td {
  padding: var(--params-td-padding);
}

.site-footer {
  background: var(--brand-black);
  color: var(--brand-grey);
  display: flex;
  font-size: var(--p-size);
  height: var(--site-footer-height);
  padding: var(--site-footer-padding);
  text-align: center;
  width: 100%;
}

.site-header {
  background: white;
  display: flex;
  flex-direction:column;
  //justify-content: space-between;
  height: var(--site-header-height);
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 10;

}

.site-header-breadcrumbs {
  align-items: center;
  //background: var(--brand-white);
  border: var(--site-header-breadcrumbs-border);
  display: flex;
  height: var(--site-header-breadcrumbs-height);
  padding: var(--site-header-breadcrumbs-padding);
}

ul.breadcrumb {
  margin: 0;
  padding-left: 0.5rem;
}

.site-header-title {
  align-items: center;
  border-bottom: var(--brand-border);
  color: white;
  //background: var(--brand-white);
  display: flex;
  height: var(--site-header-title-height);
  justify-content: space-between;
  width: 100%;
}
/*
 * The main background is set to 'white'
 */
.site {
  background: white;
}

.site-wrapper {
  background: white;
  position: relative;
}
/*
 * The current website is based on a single column layout,
 * centered and with a maximum width of 1600px
 */
.set-maxwidth {
  max-width: 1600px;
  margin: 0 auto 0 auto;
}

.site-wrapper {
  display: flex;
  flex-direction: column;
}

.td-desc {
  width: 100%;
}

.toc-h1, .toc-h2, .toc-h3, .toc-h4 {
  color: var(--brand-grey);
  font-size: 0.95rem;
}

.toc-h1 {
  font-size: 1.0rem;
  font-weight: 600;
  list-style-type: none;
}

.toc-body ul {
  padding:0;
  list-style: none;
  margin-left: 0.75rem;
}

.toc-body > ul {
  padding: 0;
  padding-left: 0.5rem;
}

.toc-body li {
  padding-left: 0.75em;
}

.toc-body li:before {
  content: "\f054";
  font-family: "Font Awesome\ 5 Free";
  font-weight: 900;
  display: inline-block;
  margin-left: -0.75em;
  width: 1.25em;
}

.toc-body > ul > li {
  padding:0;
}

.toc-body > ul > li:before {
  content: "";
}

.toc-header {
  color: var(--brand-grey);
  font-size: 1.15rem;
  font-weight: 600;
  margin: 1.0rem 0.5rem 0.5rem 0.5rem;
}

.toc-header .fa {
  margin-right: 0.5rem;
}
